[x-cloak] {
  display: none !important;
}

.hud-only {
  display: none;
}
.hud-only.-keymap {
  @apply px-2 rounded-sm bg-black;
}

.-hud .hud-only {
  display: inherit;
}

.c-input input[type=text],
.c-input input[type=password],
.c-input input[type=email],
.c-input input[type=number],
.c-input input[type=url],
.c-input input[type=date],
.c-input input[type=datetime-local],
.c-input input[type=month],
.c-input input[type=week],
.c-input input[type=time],
.c-input input[type=search],
.c-input input[type=tel],
.c-input textarea,
.c-input select {
  @apply py-2 px-4 text-dark bg-light rounded w-full;
}
.c-input input[type=text]:active, .c-input input[type=text]:focus,
.c-input input[type=password]:active,
.c-input input[type=password]:focus,
.c-input input[type=email]:active,
.c-input input[type=email]:focus,
.c-input input[type=number]:active,
.c-input input[type=number]:focus,
.c-input input[type=url]:active,
.c-input input[type=url]:focus,
.c-input input[type=date]:active,
.c-input input[type=date]:focus,
.c-input input[type=datetime-local]:active,
.c-input input[type=datetime-local]:focus,
.c-input input[type=month]:active,
.c-input input[type=month]:focus,
.c-input input[type=week]:active,
.c-input input[type=week]:focus,
.c-input input[type=time]:active,
.c-input input[type=time]:focus,
.c-input input[type=search]:active,
.c-input input[type=search]:focus,
.c-input input[type=tel]:active,
.c-input input[type=tel]:focus,
.c-input textarea:active,
.c-input textarea:focus,
.c-input select:active,
.c-input select:focus {
  @apply outline-none shadow-outline;
}
.c-input select:not([multiple]) {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2212%22%20height%3D%2212%22%20viewBox%3D%220%200%2012%2012%22%3E%3Ctitle%3Edown-arrow%3C%2Ftitle%3E%3Cg%20fill%3D%22%23000000%22%3E%3Cpath%20d%3D%22M10.293%2C3.293%2C6%2C7.586%2C1.707%2C3.293A1%2C1%2C0%2C0%2C0%2C.293%2C4.707l5%2C5a1%2C1%2C0%2C0%2C0%2C1.414%2C0l5-5a1%2C1%2C0%2C1%2C0-1.414-1.414Z%22%20fill%3D%22%23000000%22%3E%3C%2Fpath%3E%3C%2Fg%3E%3C%2Fsvg%3E");
  background-size: 0.6em;
  background-repeat: no-repeat;
  background-position: calc(100% - 1.25em) center;
}
input[type=text].a-input,
input[type=password].a-input,
input[type=email].a-input,
input[type=number].a-input,
input[type=url].a-input,
input[type=date].a-input,
input[type=datetime-local].a-input,
input[type=month].a-input,
input[type=week].a-input,
input[type=time].a-input,
input[type=search].a-input,
input[type=tel].a-input,
select.a-input,
textarea.a-input {
  @apply py-2 px-4 text-dark bg-light rounded w-full;
}

select.a-input:not([multiple]) {
  padding-right: 2.5rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2212%22%20height%3D%2212%22%20viewBox%3D%220%200%2012%2012%22%3E%3Ctitle%3Edown-arrow%3C%2Ftitle%3E%3Cg%20fill%3D%22%23000000%22%3E%3Cpath%20d%3D%22M10.293%2C3.293%2C6%2C7.586%2C1.707%2C3.293A1%2C1%2C0%2C0%2C0%2C.293%2C4.707l5%2C5a1%2C1%2C0%2C0%2C0%2C1.414%2C0l5-5a1%2C1%2C0%2C1%2C0-1.414-1.414Z%22%20fill%3D%22%23000000%22%3E%3C%2Fpath%3E%3C%2Fg%3E%3C%2Fsvg%3E");
  background-size: 0.6em;
  background-repeat: no-repeat;
  background-position: calc(100% - 1.25em) center;
}

.ls-input.--toggle-input input[type=checkbox] + .toggle {
  width: 60px;
  height: 32px;
  padding: 4px;
  border-radius: 42px;
  background: #B7BABF;
  box-sizing: border-box;
  transition: background 0.5s ease;
}
.ls-input.--toggle-input input[type=checkbox] + .toggle .toggle-pill {
  display: block;
  width: 24px;
  height: 24px;
  background: white;
  border-radius: 20px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  margin-left: 0;
  transition: margin 0.08s cubic-bezier(0.77, 0.49, 1, 1);
}

.ls-input.--toggle-input input[type=checkbox]:checked + .toggle {
  background: #3076F6;
  transition: background 0.5s ease;
}
.ls-input.--toggle-input input[type=checkbox]:checked + .toggle .toggle-pill {
  margin-left: calc(100% - 24px);
}

.ls-input.--toggle-input input[type=checkbox]:focus + .toggle {
  background: #3076F6;
}

.ls-input.--toggle-input input[type=checkbox] {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
}

.o-cp {
  @apply m-0 flex w-full h-screen overflow-hidden;
  background-color: var(--ls-color-light);
}
.o-cp__sidebar {
  @apply w-3/12 shadow-sm h-full;
  max-width: 300px;
}
.o-cp__topbar {
  @apply w-full shadow-sm h-20;
}
.o-cp__main {
  @apply flex-grow flex flex-col overflow-y-scroll;
}
.o-cp__content {
  @apply flex-grow;
}

.c-topbar {
  @apply m-0 px-4 h-full w-full flex items-center;
  color: var(--ls-color-dark);
  background-color: var(--ls-color-white);
}
.c-topbar__buttons {
  @apply flex-grow;
}

.c-sidebar {
  @apply m-0 p-0 flex flex-col min-w-full min-h-screen text-white bg-primary;
}
.c-sidebar__brand {
  @apply flex items-center justify-start h-20 p-8;
}
.c-sidebar__brand a {
  @apply text-2xl text-white;
}
.c-sidebar__nav {
  @apply flex-grow p-4;
}
.c-sidebar__nav nav > a {
  @apply block p-4 rounded-lg;
}
.c-sidebar__nav nav > a.-active {
  @apply font-bold;
  background-color: #00000015;
}
.c-sidebar__nav nav ul {
  @apply mb-4;
}
.c-sidebar__nav nav ul li {
  @apply list-disc ml-4;
}
.c-sidebar__nav nav ul li a {
  @apply block p-1 pl-0;
}
.c-sidebar__nav nav ul li a.-active {
  @apply font-bold;
}

.c-card {
  @apply m-4 bg-white rounded-lg;
}
.c-card__content {
  @apply p-4;
}

.c-form__fields {
  @apply flex flex-wrap;
}
.c-form__field {
  @apply w-full my-2;
}
.c-form__controls {
  @apply flex items-center;
}
.c-form__controls > nav:first-of-type {
  @apply flex-grow mr-2;
}

.c-field > label {
  @apply font-bold mb-2 inline-block;
}

.c-field__required {
  color: var(--color-red);
}

.c-table table {
  min-width: 100%;
}
.c-table table thead tr th {
  @apply px-8 py-4 text-left font-medium;
  background-color: rgba(0, 0, 0, 0.1);
}
.c-table table thead tr th.c-table__selector {
  width: 1px;
  @apply px-8 pr-0;
}
.c-table table thead tr th svg {
  @apply h-5 w-5 inline align-middle ml-1 opacity-50;
}
.c-table table tbody tr {
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;
}
.c-table table tbody tr:hover {
  @apply shadow-sm;
  border: 1px solid rgba(0, 0, 0, 0.05);
  background-color: rgba(0, 0, 0, 0.025);
}
.c-table table tbody tr td {
  @apply px-8 py-3 whitespace-nowrap;
}
.c-table table tbody tr td.c-table__selector {
  width: 1px;
  @apply px-8 pr-0;
}
.c-table__content {
  @apply shadow-sm m-4 bg-white rounded-lg overflow-hidden;
}
.c-table__buttons nav {
  @apply justify-end;
}
.c-table__buttons .a-button {
  @apply font-medium px-2 py-1;
}
.c-table__limiter select {
  @apply w-auto;
}
.c-table__actions, .c-table__limiter, .c-table__meta {
  @apply px-8 py-4 whitespace-nowrap;
}

.c-filters {
  @apply shadow-sm m-4 bg-white rounded-lg overflow-hidden p-4;
}
.c-filters form {
  @apply flex gap-4;
}
.c-filters__inputs {
  @apply flex gap-4;
}

.c-buttons {
  @apply flex justify-items-center items-center gap-1;
}

.a-button {
  @apply py-2 px-4 font-bold inline-block rounded text-white bg-primary cursor-pointer;
}
.a-button span {
  color: var(--ls-color-white);
}
.a-button.-primary {
  color: var(--ls-color-white);
  background-color: var(--ls-color-primary);
}
.a-button.-secondary {
  color: var(--ls-color-white);
  background-color: var(--ls-color-secondary);
}
.a-button.-link {
  color: var(--ls-color-black);
  background-color: var(--ls-color-transparent);
}

.c-shortcuts {
  @apply flex items-center gap-4;
}
.c-shortcuts > div {
  position: relative;
}
.c-shortcuts > div > a,
.c-shortcuts > div > button {
  @apply h-10 w-10 p-0 inline-block border-0 border-transparent outline-none flex items-center justify-center rounded-full bg-transparent cursor-pointer;
}
.c-shortcuts > div > a:active, .c-shortcuts > div > a:focus,
.c-shortcuts > div > button:active,
.c-shortcuts > div > button:focus {
  @apply shadow-outline;
}
.c-shortcuts > div > a svg,
.c-shortcuts > div > a img,
.c-shortcuts > div > button svg,
.c-shortcuts > div > button img {
  @apply h-10 w-10;
}
.c-shortcuts > div > a svg,
.c-shortcuts > div > button svg {
  @apply text-dark;
}
.c-shortcuts > div > a img,
.c-shortcuts > div > button img {
  @apply rounded-full;
}

.c-dropdown__content {
  @apply block absolute top-full right-0 shadow-sm bg-white rounded-lg overflow-hidden mt-2;
}
.c-dropdown__content a {
  @apply whitespace-nowrap px-4 py-2 block;
}
.c-dropdown__content a:hover {
  @apply bg-secondary text-white;
}